<template>
  <div class="advertpage">
    <van-button type="default" size="small" round @click="jump">跳过广告{{num}}</van-button>
    <img src="../../assets/guanggao.jpg" alt />
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 3,
      cleardate: null
    };
  },
  methods: {
    jump() {
      // 关闭定时器
      clearInterval(this.cleardate);
      // 跳转
      this.$router.push({ name: "homepage" });
    }
  },
  created() {
     this.$store.state.vantTab = false;
    //  console.log(this.$store.state.vantTab)
    // 倒计时
    this.cleardate = setInterval(() => {
      if (this.num == 0) {
        this.$router.push({ name: "homepage" });
      } else {
        this.num--;
      }
    }, 1000);
    // //隐藏导航
   
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    // 关闭定时器
    clearInterval(this.cleardate);
  }
};
</script>

<style lang="less" scoped>
.advertpage {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
  }
  button {
    position: absolute;
    right: 10px;
    top: 10px;
    opacity: 0.7;
  }
}
</style>